@import '@docsearch/css/dist/style.css';
@tailwind base;
@tailwind components;
@tailwind utilities;
.page-content h1 {
  @apply text-4xl;
}
.page-content h2 {
  @apply text-3xl;
}
.page-content h3 {
  @apply text-2xl;
}
.page-content h4 {
  @apply text-xl;
}
.page-content h5 {
  @apply text-lg;
}
.page-content h6 {
  @apply text-base;
}
.page-content h1, .page-content h2, .page-content h3, .page-content h4, .page-content h5, .page-content h6 {
  scroll-margin-top: 6rem;
  @apply font-medium;
  @apply leading-tight;
}
.page-content ol {
  @apply list-[upper-alpha];
  @apply pl-5;
}
.page-content ol li::marker {
  @apply font-semibold;
  @apply text-black;
}
.page-content ol ol {
  @apply list-decimal;
}
.page-content ol ol ol {
  @apply list-[lower-alpha];
}
.page-content ol ol ol ol {
  @apply list-[lower-roman];
}
.page-content img {
  @apply w-full;
}
.page-content figure p {
  @apply w-full;
}

table th,
table td {
  @apply p-1;
  @apply align-top;
}

.table-bordered th {
  @apply border-b-2;
}
.table-bordered td {
  @apply border-t;
}

.table-scroll table {
  @apply w-full;
}
.table-scroll th,
.table-scroll td {
  @apply whitespace-nowrap;
}

.table-hidden-thead thead {
  @apply hidden;
}

.algolia-autocomplete {
  @apply flex;
  @apply h-full;
}

.ais-Highlight-highlighted {
  @apply font-bold;
  @apply border-b-2;
  @apply border-green-700;
}

.ais-Snippet-highlighted {
  @apply font-bold;
  @apply text-gray-900;
  @apply border-b-2;
  @apply border-green-700;
}

@keyframes slideInUp {
  0% {
    opacity: 0;
    transform: translateY(0) scale(0.75);
    transform-origin: var(--radix-tooltip-content-transform-origin);
  }
  100% {
    opacity: 0.9;
    transform: translateY(5px) scale(1);
    transform-origin: var(--radix-tooltip-content-transform-origin);
  }
}
@keyframes slideOutDown {
  0% {
    opacity: 0.9;
    transform: translateY(5px) scale(1);
    transform-origin: var(--radix-tooltip-content-transform-origin);
  }
  100% {
    opacity: 0;
    transform: translateY(0) scale(0.75);
    transform-origin: var(--radix-tooltip-content-transform-origin);
  }
}
@keyframes slideInDown {
  0% {
    opacity: 0;
    transform: translateY(0) scale(0.75);
    transform-origin: var(--radix-tooltip-content-transform-origin);
  }
  100% {
    opacity: 0.9;
    transform: translateY(-5px) scale(1);
    transform-origin: var(--radix-tooltip-content-transform-origin);
  }
}
@keyframes slideOutUp {
  0% {
    opacity: 0.9;
    transform: translateY(-5px) scale(1);
    transform-origin: var(--radix-tooltip-content-transform-origin);
  }
  100% {
    opacity: 0;
    transform: translateY(0) scale(0.75);
    transform-origin: var(--radix-tooltip-content-transform-origin);
  }
}
.rdx-tooltip[data-state=instant-open][data-side=top] {
  animation: slideInDown 100ms ease-in-out forwards;
}
.rdx-tooltip[data-state=instant-open][data-side=bottom] {
  animation: slideInUp 100ms ease-in-out forwards;
}
.rdx-tooltip[data-state=closed][data-side=top] {
  animation: slideOutUp 100ms ease-in-out forwards;
}
.rdx-tooltip[data-state=closed][data-side=bottom] {
  animation: slideOutDown 100ms ease-in-out forwards;
}

.DocSearch.DocSearch-Button {
  @apply w-full;
}